import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	name: "user",
	data() {
		return {
			menuBorder: false,
			menuArrow: true,
			menuCard: true,
			gridCol: 3,
			gridBorder: false
		};
	},
	computed: mapState(['userInfo']),
	methods: {
		...mapMutations(['setLogoutInfo']),
		toChild(e) {
			uni.navigateTo({
				url: e.currentTarget.dataset.url
			})
		},
		logout(){
			uni.showModal({
				title: '提示',
				content: '确定退出登录？',
				showCancel: true,
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if(res.cancel) return;
					uni.showLoading({
						title: '退出登录中',
						mask: false
					});
					global.$http.post('/core/login/logout', {
						params: {},
					}).then(res => {
						uni.hideLoading();
						if (res.status === "0") {
							//清除密码
							this.setLogoutInfo();
							//跳转登陆页
							uni.reLaunch({
								url:"../account/login"
							});
						} else {
							uni.showToast({
								title: res.msg,
								icon: 'none'
							});
						}
					}).catch(err => {
						uni.hideLoading();
						uni.showToast({
							title: err.message,
							icon: 'none'
						});
					});
				},
				fail: () => {},
				complete: () => {}
			});
			
		}
	},
}
